<template>
  <div class="hs-container">
    <c-header leftIcon1="home_2.png" :leftFn1="_gotoHomePage" :title="$t('base.DomesticHotelOrderConfirmation')"></c-header>
    <hs-scroll class="table-view" v-if="!noValue(JSON.stringify(hotel))">
      <ul>
        <hs-section>
          <hs-cell-1 :showLogo="false">
            <div class="state-details">
              <div class="header">
                <p>{{Number(hotelOrder.TravelType) === 0 ? $t('base.BusinessTrip') : $t('base.PersonalTrip')}}</p>
              </div>
              <p class="infor">{{$t('base.OrderDate')}}: {{$t(beTime(new Date()), 'date', ['yyyy-MM-dd', 'MM dd, yyyy'])}}&nbsp;{{beTime(new Date(), 'hh:mm')}}</p>
              <div class="buttons">
                <template v-if="!submitSuccess">
                  <hs-button v-if="Number(hotelOrder.PayType) === 2 && noValue(JSON.stringify(hotelOrder.VettingProcessList))" class="button" @click="_gotoSubmitAndPay">{{$t('base.SubmitAndPay')}}</hs-button>
                  <hs-button v-else class="button" @click="_confirm">{{$t('base.Submit')}}</hs-button>
                </template>
                <template v-else>
                  <hs-button class="button" v-if="Number(hotelOrder.PayType) === 2 && noValue(JSON.stringify(hotelOrder.VettingProcessList))" @click="_gotoPay">{{$t('base.Pay')}}</hs-button>
                </template>
              </div>
            </div>
          </hs-cell-1>
        </hs-section>
        <hs-section>
          <hs-cell-1 :showLogo="false">
            <div class="custom-cell-hotel">
              <p>{{hotel.HotelName}}</p>
              <p>{{hotelOrder.RoomAmount}}{{$t('base.QuantifierHotel')}} {{ratePlan.RatePlanName}}</p>
              <p>{{room.BedType}} | {{$t('base.XPeoplePerRoom', 'function', room.Capcity)}} | {{ratePlan.BreakFast}}</p>
              <p>{{$t('base.CheckInDate')}} {{hotelOrder.CheckInDate}}</p>
              <p>{{$t('base.CheckOutDate')}} {{hotelOrder.CheckOutDate}} {{$t('base.TotalNights', 'function', getApartDayCount(hotelOrder.CheckInDate, hotelOrder.CheckOutDate))}}</p>
              <p v-if="hotelOrder.ArrivalTime">{{$t('base.GoToTheStoreBeforeOClock', 'function', hotelOrder.ArrivalTime)}}</p>
            </div>
          </hs-cell-1>
          <hs-cell-1 :showLogo="false">
            <div class="custom-cell-cancel">
              <div style="background-image: url('static/image/hotel/irreversible.png')"></div>
              <div>
                <p>{{ratePlan.CancelRuleDesc}}</p>
              </div>
            </div>
          </hs-cell-1>
          <hs-cell-1 @click.native="showMap = true" logoName="address.png">
            <div class="custom-cell-title">
              <div class="right">
                <div class="address">
                  <div class="logo" style="background-image: url('static/image/hotel/location.png')"></div>
                  <p>{{hotelOrder.CityName}}{{hotel.DistrictName}}{{hotel.Address}}</p>
                </div>
              </div>
            </div>
          </hs-cell-1>
          <hs-cell-1 logoName="call.png" @click.native="_callPhone(hotel.Phone)">
            <div class="custom-cell-title">
              <div class="right">
                <p>{{$t('base.ContactHotel')}}：{{hotel.Phone}}</p>
              </div>
            </div>
          </hs-cell-1>
        </hs-section>
        <hs-section>
          <hs-cell-1 :showLogo="false" :showSeparator="false" v-for="(item, index) in hotelOrder.Guests" :key="index">
            <div class="custom-cell-people">
              <div class="left" :style="{opacity: index === 0 ? 1 : 0}">
                <p>{{$t('base.Roomer')}}</p>
              </div>
              <div class="right" :style="{'border-bottom-width': index === hotelOrder.Guests.length - 1 ? '0px' : '1px'}">
                <div class="name">{{item.Name}}</div>
                <div class="others">
                  <p>{{$t('base.CellphoneNumber')}} {{`${item.Mobile ? item.Mobile : $t('base.Unfilled')}`}}</p>
                </div>
              </div>
            </div>
          </hs-cell-1>
        </hs-section>
        <hs-section>
          <hs-cell-small-title v-if="hotelOrder.SpecificRequirements"
            :title="$t('base.SpecificRequirements')"
            type="bold"
            :value="hotelOrder.SpecificRequirements"
            :showLogo="false">
          </hs-cell-small-title>
          <hs-cell-small-title v-if="hotelOrderInit.IsDisplayCustomItem && hotelOrder.CustomItem && hotelOrder.CustomItem.Name"
            :title="hotelOrderInit.CustomItemName"
            type="bold"
            :value="hotelOrder.CustomItem.Name"
            :showLogo="false">
          </hs-cell-small-title>
          <hs-cell-small-title v-if="hotelOrderInit.IsDisplayPurpose"
            :title="$t('base.Purpose')"
            type="bold"
            :value="hotelOrder.Purpose"
            :showLogo="false">
          </hs-cell-small-title>
          <!-- TODO: 酒店授权码填写后台支持, 但是订单详情接口没有此字段, 所以暂时隐藏 -->
          <!-- <hs-cell-3 v-if="hotelOrderInit.IsDisplayAuthorizationCode" title="授权码" :showLogo="false">
            <p class="cell-3-value">{{hotelOrder.AuthorizationCode}}</p>
          </hs-cell-3> -->
          <hs-cell-small-title v-if="Number(hotelOrder.TravelType) === 0" @click.native="showCExamineProcess = true"
            :class="{disabled: noValue(JSON.stringify(hotelOrder.VettingProcessList))}"
            :title="$t('base.ApprovalInformation')"
            type="bold"
            :value="noValue(JSON.stringify(hotelOrder.VettingProcessList)) ? $t('base.NoApprovalRequired') : (language === 'CN' ? '' : $t('base.ApprovalInformation'))"
            :showLogo="!noValue(JSON.stringify(hotelOrder.VettingProcessList))">
          </hs-cell-small-title>
          <hs-cell-small-title v-if="Number(hotelOrder.TravelType) === 0"
            :title="$t('base.PaymentWay')"
            type="bold"
            :value="hotelOrder.PayType | filterPayType"
            :showLogo="false">
          </hs-cell-small-title>
          <hs-cell-small-title v-if="hotelOrder.ViolationRankReason"
            :title="$t('base.ViolationOfRank')"
            type="bold"
            :value="hotelOrder.ViolationRankReason"
            :warning="false"
            :showLogo="false"
            @header="_getTravelStandard">
          </hs-cell-small-title>
        </hs-section>
        <hs-section>
          <hs-cell-1 :showLogo="false" :showSeparator="false" v-for="(item, index) in hotelOrder.Contacts" :key="index">
            <div class="custom-cell-people">
              <div class="left" :style="{opacity: index === 0 ? 1 : 0}">
                <p>{{$t('base.ContactInformation')}}</p>
              </div>
              <div class="right" :style="{'border-bottom-width': index === hotelOrder.Contacts.length - 1 ? '0px' : '1px'}">
                <div class="name">{{item.Name}}</div>
                <div class="others">
                  <p>{{$t('base.CellphoneNumber')}} {{item.Mobile ? item.Mobile : $t('base.Unfilled')}}</p>
                  <p>{{$t('base.Email')}} {{item.Email ? item.Email : $t('base.Unfilled')}}</p>
                </div>
              </div>
            </div>
          </hs-cell-1>
        </hs-section>
      </ul>
    </hs-scroll>
    <div class="footer" @click="_switchDetailsDialog">
      <div class="price">
        <p>{{$t('base.TotalAmount')}}</p>
        <p v-if="hotelOrderInit && hotelOrderInit.ChargeInfo">￥{{hotelOrderInit.ChargeInfo.TotalPrice}}</p>
      </div>
      <div class="details">
        <div class="logo" :style="{transform: showDetailsDialog ? 'rotate(90deg)' : 'rotate(-90deg)'}" style="background-image: url('static/image/base/cell/right_arrow_gray.png')"></div>
      </div>
    </div>
    <hs-dialog v-model="showDetailsDialog" height="calc(100% - 50px)" ref="detailsDialog">
      <hs-scroll class="detailsDialog" :data="[hotelOrder]" @click.native.stop v-if="hotelOrderInit && hotelOrderInit.ChargeInfo">
        <ul>
          <hs-cell-1 :showLogo="false">
            <div class="detailsDialog-custom-cell">
              <p>{{$t('base.PriceHotel')}}</p>
              <p>
                <span>￥{{hotelOrderInit.ChargeInfo.AverageRate * getApartDayCount(hotelOrder.CheckInDate, hotelOrder.CheckOutDate)}}</span>
                <span>x {{getApartDayCount(hotelOrder.CheckInDate, hotelOrder.CheckOutDate)}}{{$t('base.QuantifierNight')}}</span>
                <span>x {{hotelOrder.RoomAmount}}{{$t('base.QuantifierHotel')}}</span>
              </p>
            </div>
          </hs-cell-1>
          <hs-cell-1 :showLogo="false">
            <div class="detailsDialog-custom-cell">
              <p>{{$t('base.ServiceFee')}}</p>
              <p><span>￥{{hotelOrderInit.ChargeInfo.ServiceCharge}}</span></p>
            </div>
          </hs-cell-1>
        </ul>
      </hs-scroll>
    </hs-dialog>
    <infor-details-map v-model="showMap" :details="hotel"></infor-details-map>
    <c-examine-process v-model="showCExamineProcess" :details="hotelOrder" :passengerWarning="$t('base.Roomer')"></c-examine-process>
    <c-travel-standard v-model="showTravelStandard" :type="2" :details="hotelRight.HotelRank" :travelPolicy="hotelRight.TravelPolicy"></c-travel-standard>

    <c-order-success ref="COrderSuccess" :XOrder="hotelOrder" :OrderBusinessType="2"></c-order-success>
    <pay ref="Pay"></pay>
  </div>
</template>
<script type="text/ecmascript-6">
import CExamineProcess from 'components/c-examine-process/c-examine-process-confirm'
import CHeader from 'components/c-header/c-header'
import COrderSuccess from 'components/c-order-success/c-order-success.vue'
import CTravelStandard from 'components/c-travel-standard/c-travel-standard'
import HsButton from 'base/hs-button/hs-button'
import HsCell1 from 'base/hs-tableview/hs-cell-1/hs-cell-1'
import HsCellSmallTitle from 'base/hs-tableview/hs-cell-small-title/hs-cell-small-title'
import HsDialog from 'base/hs-dialog/hs-dialog'
import HsScroll from 'base/hs-scroll/hs-scroll'
import HsSection from 'base/hs-tableview/hs-section/hs-section'
import InforDetailsMap from '../../infor-details/infor-details-map'
import Pay from 'pages/pay/pay'
import Vue from 'vue'
import {mapGetters, mapMutations, mapActions} from 'vuex'
import {kpiHotelSaveOrder} from 'api'
export default {
  beforeRouteEnter(to, from, next) {
    next(vm => {
      if (vm.noValue(vm.hotelOrder.TravelType)) {
        vm.$router.replace('/home')
      }
    })
  },
  beforeRouteLeave (to, from, next) {
    if (this.showMap || this.showCExamineProcess) {
      this.showMap = false
      this.showCExamineProcess = false
      next(false)
    } else {
      next()
    }
  },
  computed: {
    ...mapGetters([
      'hotelOrder',
      'hotelRight'
    ]),
    hotelOrderInit() {
      console.log('hotelOrderInit: ', this.$route.params.hotelOrderInit)
      return this.$route.params.hotelOrderInit
    },
    hotel() {
      console.log('hotel: ', this.$route.params.hotel)
      return this.$route.params.hotel
    },
    room() {
      console.log('room: ', this.$route.params.room)
      return this.$route.params.room
    },
    ratePlan() {
      console.log('ratePlan: ', this.$route.params.ratePlan)
      return this.$route.params.ratePlan
    }
  },
  filters: {
    filterPayType(val) {
      switch (val) {
        case 1:
          return Vue.prototype.$t('base.CompanyPayment')
        case 2:
          return Vue.prototype.$t('base.PersonalPayment')
        case 3:
          return `${Vue.prototype.$t('base.CompanyPayment')}(${Vue.prototype.$t('base.Prepayments')})`
        default:
          return Vue.prototype.$t('base.Unknown')
      }
    }
  },
  data() {
    return {
      showMap: false,
      showDetailsDialog: false,
      showCExamineProcess: false,
      submitSuccess: false,
      showTravelStandard: false
    }
  },
  methods: {
    ...mapMutations([
      'SET_HOTEL_ORDER'
    ]),
    ...mapActions([
      'dispatchHotelRight'
    ]),
    // 提交/保存订单
    _confirm() {
      this.$alert({
        content: this.$t('TM.AreYouSureToSubmitYourOrder'),
        cancelTxt: this.$t('base.Cancel'),
        confirmFn: () => {
          let params = {...this.hotelOrder}
          params.CheckInDate = this.beTimeNumber(this.hotelOrder.CheckInDate)
          params.CheckOutDate = this.beTimeNumber(this.hotelOrder.CheckOutDate)
          kpiHotelSaveOrder(params).then((res) => {
            console.log('保存订单: ', res)
            if (res.ResultData.OrderID) {
              this.submitSuccess = true
              /** 提交成功后 */
              let hotelOrder = JSON.parse(JSON.stringify(this.hotelOrder))
              hotelOrder.OrderID = res.ResultData.OrderID
              this.SET_HOTEL_ORDER(hotelOrder)
              this.$refs.COrderSuccess.open()
              // this.$alert({
              //   content: Number(this.hotelOrder.PayType) === 2 ? this.$t('TM.TheCurrentOrderIsAPersonalPaymentOrder') : this.$t('TM.OrderSubmissionSuccessful'),
              //   confirmTxt: this.$t('base.CheckOrder'),
              //   cancelTxt: this.$t('base.ContinueBooking'),
              //   confirmFn: () => {
              //     this.$router.replace({name: 'hotelOrderList', params: {TravelType: Number(this.hotelOrder.TravelType)}})
              //   },
              //   cancelFn: () => {
              //     this.$router.replace({path: '/hotel/query', query: {TravelType: this.hotelOrder.TravelType}})
              //   }
              // })
              /********************************************************************************************************************/
            } else {
              this.$toast(this.$t('TM.TheSubmissionFailedPleaseTryAgainLater'))
            }
          })
        }
      })
    },
    // 提交并支付
    _gotoSubmitAndPay() {
      this.$alert({
        content: this.$t('TM.AreYouSureToSubmitAndPayForTheOrder'),
        cancelTxt: this.$t('base.Cancel'),
        confirmFn: () => {
          let params = {...this.hotelOrder}
          params.CheckInDate = this.beTimeNumber(this.hotelOrder.CheckInDate)
          params.CheckOutDate = this.beTimeNumber(this.hotelOrder.CheckOutDate)
          kpiHotelSaveOrder(params).then((res) => {
            console.log('保存订单: ', res)
            if (res.ResultData.OrderID) {
              this.submitSuccess = true
              let hotelOrder = JSON.parse(JSON.stringify(this.hotelOrder))
              hotelOrder.OrderID = res.ResultData.OrderID
              this.SET_HOTEL_ORDER(hotelOrder)
              this._gotoPay()
            } else {
              this.$toast(this.$t('TM.TheSubmissionFailedPleaseTryAgainLater'))
            }
          })
        }
      })
    },
    /** 支付 */
    _gotoPay(item) {
      this.$refs.Pay && this.$refs.Pay.gotoPay({
        OrderBusinessType: 2,
        OrderID: this.hotelOrder.OrderID,
        PayWay: 3,
        TravelType: Number(this.hotelOrder.TravelType),
        CallbackPathName: 'hotelOrderList'
      })
    },
    // 打电话
    _callPhone(mobile) {
      window.location.href = `tel:${mobile}`
    },
    _switchDetailsDialog() {
      if (this.showDetailsDialog) {
        this.$refs.detailsDialog.showDetails = false
      } else {
        this.showDetailsDialog = true
      }
    },
    _gotoHomePage() {
      this.$router.replace('/home')
    },
    // 获取差旅职级
    _getTravelStandard() {
      this.dispatchHotelRight().then((res) => {
        this.showTravelStandard = true
      })
    }
  },
  components: {
    CHeader,
    COrderSuccess,
    HsDialog,
    HsScroll,
    HsSection,
    HsCell1,
    HsCellSmallTitle,
    HsButton,
    InforDetailsMap,
    CExamineProcess,
    CTravelStandard,
    Pay
  }
}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
@import '~common/stylus/base'
@import '~common/stylus/variable'
.hs-container
  width 100%
  height 100%
  >.table-view
    width 100%
    height calc(100% - 45px - 50px)
    overflow hidden
    .state-details
      width 100%
      padding 15px 0
      background-color $color_white
      >.header
        @extend .flex-row
        @extend .flex-start
        width 100%
        margin-bottom 8px
        font-size 1.05rem
      >.infor
        width 100%
        font-size 0.8rem
        color $color_gray_text
        line-height 1.4em
      >.buttons
        @extend .flex-row
        width 100%
        >.button
          flex 1
          margin 10px 5px 0 5px
          &:first-child
            margin-left 0px
          &:last-child
            margin-right 0px
    .custom-cell-hotel
      @extend .flex-column
      width 100%
      padding 15px 0
      >p
        line-height 1.5em
        &:nth-child(1)
          font-size 1.1rem
          font-weight bold
        &:nth-child(2)
          font-size 0.8rem
          font-weight bold
        &:nth-child(3)
        &:nth-child(4)
        &:nth-child(5)
          font-size 0.8rem
          color $color_gray_text
    .custom-cell-cancel
      @extend .flex-row
      @extend .flex-start
      width 100%
      padding 15px 0
      >div
        &:nth-child(1)
          width 30px
          height 30px
          background-size 100% 100%
          margin-right 10px
        &:nth-child(2)
          width calc(100% - 40px)
          >p
            line-height 1.3em
            &:nth-child(1)
              font-size 0.9rem
              color $color_theme
            &:nth-child(2)
              font-size 0.8rem
              color $color_gray_text
    .custom-cell-title
      @extend .flex-row
      @extend .flex-start
      width 100%
      padding 15px 0
      >.right
        width calc(100% - 60px)
        >p
          font-size 0.9rem
          color $color_black_text_0
          line-height 1.5em
        >.name
          font-size 1.2rem
          font-weight bold
          line-height 1.2em
        >.logos
          @extend .flex-row
          @extend .flex-start
          flex-wrap wrap
          width 100%
          margin 3px 0
          >.logo
            width 15px
            height 15px
            background-size 100% 100%
            margin 5px 10px 5px 0
        >.address
          @extend .flex-row
          @extend .flex-start
          @extend .flex-top
          width 100%
          >.logo
            width 16px
            height 16px
            background-size 100% 100%
            margin-right 4px
            margin-top 2px
          >p
            width calc(100% - 20px)
            font-size 0.8rem
            color $color_black_text_0
            line-height 1.5em
      >.left
        width 60px
        font-size 0.8rem
        color $color_theme
        text-align right
    .custom-cell-people
      @extend .flex-between
      @extend .flex-start
      @extend .flex-top
      width 100%
      >.left
        width 90px
        padding 15px 0
        >p
          @extend .flex-row
          @extend .flex-start
          font-size 1rem
          color $color_black_text_1
          line-height 1.4em
      >.right
        width calc(100% - 100px)
        padding 15px 0
        border-bottom 1px dashed $color_gray_layer
        >.name
          @extend .flex-row
          @extend .flex-start
          font-size 1rem
          font-weight bold
          line-height 1.4em
        >.others
          width 100%
          >p
            line-height 1.5em
            color $color_gray_text
            font-size 1rem
            &:first-child
              margin-top 7px
    .cell-3-value
      font-weight bold
    .cell-3-title
      @extend .flex-row
      @extend .flex-start
      width 100%
      >.logo
        width 20px
        height 20px
        background-size 100% 100%
        margin-left 3px
  >.footer
    @extend .flex-row
    @extend .flex-between
    width 100%
    height 50px
    background-color $color_white
    border-top 1px solid $color_gray_layer
    padding 0 15px
    >.price
      @extend .flex-row
      @extend .flex-start
      width calc(100% - 30px)
      height 100%
      >p
        font-size 1rem
        &:nth-child(2)
          color $color_theme
          margin-left 5px
    >.details
      @extend .flex-row
      @extend .flex-between
      width 20px
      height 100%
      >p
        font-size 0.9rem
      >.logo
        width 20px
        height 20px
        background-size 100% 100%
        transition all 300ms
  .detailsDialog
    position absolute !important
    width 100%
    left 0
    bottom 0
    background-color $color_white
    max-height 70%
    overflow hidden
    .detailsDialog-custom-cell
      @extend .flex-row
      @extend .flex-between
      width 100%
      height 44px
      >p
        font-size 0.8rem
        &:nth-child(1)
          color $color_black_text_0
        &:nth-child(2)
          color $color_theme
          >span
            color $color_black_text_0
            &:nth-child(1)
              color $color_theme
            &:nth-child(2)
              color $color_black_text_0
</style>



